import { isArray } from '../common/index';
import { LocationServices, LocationConfig } from '../common/coreservices';
import { UIRouter } from '../router';
import { identity, unnestR, removeFrom, deregAll, extend } from '../common/common';
import { LocationLike, HistoryLike } from './interface';
import { isDefined } from '../common/predicates';
import { Disposable } from '../interface';

const /** @type {?} */ beforeAfterSubstr = (char: string) => (str: string): string[] => {
  if (!str) return ["", ""];
  let /** @type {?} */ idx = str.indexOf(char);
  if (idx === -1) return [str, ""];
  return [str.substr(0, idx), str.substr(idx + 1)];
};

export const /** @type {?} */ splitHash = beforeAfterSubstr("#");
export const /** @type {?} */ splitQuery = beforeAfterSubstr("?");
export const /** @type {?} */ splitEqual = beforeAfterSubstr("=");
export const /** @type {?} */ trimHashVal = (str) => str ? str.replace(/^#/, "") : "";

export const /** @type {?} */ keyValsToObjectR = (accum, [key, val]) => {
  if (!accum.hasOwnProperty(key)) {
    accum[key] = val;
  } else if (isArray(accum[key])) {
    accum[key].push(val);
  } else {
    accum[key] = [accum[key], val]
  }
  return accum;
};

export const /** @type {?} */ getParams = (queryString: string): any =>
    queryString.split("&").filter(identity).map(splitEqual).reduce(keyValsToObjectR, {});
/**
 * @param {?} url
 * @return {?}
 */
export function parseUrl(url: string) {
  const /** @type {?} */ orEmptyString = x => x || "";
  let [beforehash, hash] = splitHash(url).map(orEmptyString);
  let [path, search] = splitQuery(beforehash).map(orEmptyString);

  return { path, search, hash, url };
}

export const /** @type {?} */ buildUrl = (loc: LocationServices) => {
  let /** @type {?} */ path = loc.path();
  let /** @type {?} */ searchObject = loc.search();
  let /** @type {?} */ hash = loc.hash();

  let /** @type {?} */ search = Object.keys(searchObject).map(key => {
    let /** @type {?} */ param = searchObject[key];
    let /** @type {?} */ vals = isArray(param) ? param : [param];
    return vals.map(val => key + "=" + val);
  }).reduce(unnestR, []).join("&");

  return path + (search ? "?" + search : "") + (hash ? "#" + hash : "");
};
/**
 * @param {?} name
 * @param {?} isHtml5
 * @param {?} serviceClass
 * @param {?} configurationClass
 * @return {?}
 */
export function locationPluginFactory(
    name: string,
    isHtml5: boolean,
    serviceClass: { new(router?: UIRouter): LocationServices },
    configurationClass: { new(router?: UIRouter, isHtml5?: boolean): LocationConfig }
) {
  return function(router: UIRouter) {
    let /** @type {?} */ service       = router.locationService = new serviceClass(router);
    let /** @type {?} */ configuration = router.locationConfig  = new configurationClass(router, isHtml5);
/**
 * @param {?} router
 * @return {?}
 */
function dispose(router: UIRouter) {
      router.dispose(service);
      router.dispose(configuration);
    }

    return { name, service, configuration, dispose };
  };
}

