/** Predicates
 *
 * These predicates return true/false based on the input.
 * Although these functions are exported, they are subject to change without notice.
 *
 * @module common_predicates
 */
/** */

import { and, not, pipe, prop, or } from './hof';
import { Predicate } from './common'; // has or is using

import { StateObject } from '../state/stateObject';

const /** @type {?} */ toStr = Object.prototype.toString;
const /** @type {?} */ tis = (t: string) => (x: any) => typeof(x) === t;
export const /** @type {?} */ isUndefined = tis('undefined');
export const /** @type {?} */ isDefined = not(isUndefined);
export const /** @type {?} */ isNull = (o: any) => o === null;
export const /** @type {?} */ isNullOrUndefined = or(isNull, isUndefined);
export const /** @type {?} */ isFunction: (x: any) => x is Function = /** @type {?} */(( <any> tis('function')));
export const /** @type {?} */ isNumber: (x: any) => x is number = /** @type {?} */(( <any> tis('number')));
export const /** @type {?} */ isString = /** @type {?} */(( <(x: any) => x is string> tis('string')));
export const /** @type {?} */ isObject = (x: any) => x !== null && typeof x === 'object';
export const /** @type {?} */ isArray = Array.isArray;
export const /** @type {?} */ isDate: (x: any) => x is Date = /** @type {?} */(( <any> ((x: any) => toStr.call(x) === '[object Date]')));
export const /** @type {?} */ isRegExp: (x: any) => x is RegExp = /** @type {?} */(( <any> ((x: any) => toStr.call(x) === '[object RegExp]')));
export const /** @type {?} */ isState: (x: any) => x is StateObject = StateObject.isState;
/**
 * Predicate which checks if a value is injectable
 * 
 * A value is "injectable" if it is a function, or if it is an ng1 array-notation-style array
 * where all the elements in the array are Strings, except the last one, which is a Function
 * @param {?} val
 * @return {?}
 */
export function isInjectable(val: any) {
  if (isArray(val) && val.length) {
    let /** @type {?} */ head = val.slice(0, -1), /** @type {?} */ tail = val.slice(-1);
    return !(head.filter(not(isString)).length || tail.filter(not(isFunction)).length);
  }
  return isFunction(val);
}
/**
 * Predicate which checks if a value looks like a Promise
 * 
 * It is probably a Promise if it's an object, and it has a `then` property which is a Function
 */
export const isPromise = <(x: any) => x is Promise<any>> and(isObject, pipe(prop('then'), isFunction));

