/**
 * @coreapi
 * @module params
 */
/** */

import {extend, ancestors, Obj} from '../common/common';
import {StateObject} from '../state/stateObject';
/**
 * \@internalapi
 */
export class StateParams {
  [key: string]: any;
/**
 * @param {?=} params
 */
constructor(params: Obj = {}) {
    extend(this, params);
  }
/**
 * Merges a set of parameters with all parameters inherited between the common parents of the
 * current state and a given destination state.
 * 
 * @param {?} newParams
 * @param {?} $current
 * @param {?} $to
 * @return {?}
 */
$inherit(newParams: Obj, $current: StateObject, $to: StateObject) {
    let /** @type {?} */ parents = ancestors($current, $to), /** @type {?} */
        parentParams: string[], /** @type {?} */
        inherited: Obj = {}, /** @type {?} */
        inheritList: string[] = [];

    for (let /** @type {?} */ i in parents) {
      if (!parents[i] || !parents[i].params) continue;
      parentParams = Object.keys(parents[i].params);
      if (!parentParams.length) continue;

      for (let /** @type {?} */ j in parentParams) {
        if (inheritList.indexOf(parentParams[j]) >= 0) continue;
        inheritList.push(parentParams[j]);
        inherited[parentParams[j]] = this[parentParams[j]];
      }
    }
    return extend({}, inherited, newParams);
  };
}

